import React, { useEffect } from 'react';
import styles from './TapArea.module.css';
import { useSelector } from 'react-redux';
import { selectBpm, selectDifficulty, selectMute  } from '../../app/gameSlice';
import { Difficulty } from "../../app/Difficulties";
import { synth } from "../../synth";

let streakCount = 0;
let missCountTooFast = 0;
let missCountTooSlow = 0;

const MISS_COUNT_HELP_TRIGGER = 5;

function drawBpm (ctx: CanvasRenderingContext2D,
                  canvas : HTMLCanvasElement,
                  bpm : number,
                  center: number) {
  if (bpm === Infinity) { throw "BPM is not valid"; }
  if (bpm === 60000) { throw "Error computing myBpm"; }
  ctx.fillStyle = "black";
  ctx.textAlign = 'center';
  ctx.fillStyle = "white";
  ctx.font = "24px Comic Sans MS";
  const myBpmText = `${bpm.toFixed(1)}`;
  ctx.fillText(myBpmText, center, 30);
  ctx.font = "12px Comic Sans MS";
  ctx.fillText("My BPM", center - 60, 30);
}

function drawInitialState(ctx: CanvasRenderingContext2D,
                          canvas: HTMLCanvasElement,
                          center: number,
                          canvasWrapper: HTMLElement) {
  ctx.fillStyle = "red";
  ctx.fillRect(0, 0, canvas.width, canvas.height);
  ctx.textAlign = 'center';
  ctx.fillStyle = "white";
  ctx.font = "30px Comic Sans MS";
  ctx.fillText("TAP", center, 100);
  canvasWrapper.style.background = "red";
}

let oldTime : number;
let myEventListener : (event: MouseEvent) => any;

function mouseDownEventListener(bpm : number,
                                ctx : CanvasRenderingContext2D,
                                canvasWrapper: HTMLElement,
                                canvas:HTMLCanvasElement,
                                center: number,
                                difficulty: Difficulty,
                                mute: boolean) {
  if (oldTime && oldTime !== Date.now()) {
    let newTime = Date.now();
    let myDate = newTime - oldTime;
    if (myDate !== 0 && myDate !== 1) {
      oldTime = Date.now();
      let seconds = myDate / 1000;
      let myBpm = 60 / seconds;
      let offBy = bpm - myBpm;
      let helpString = '';
      if (offBy > difficulty.time) {
        streakCount = 0;
        missCountTooFast = 0;
        missCountTooSlow++;
        if (missCountTooSlow > MISS_COUNT_HELP_TRIGGER) {
          helpString = "TAP FASTER";
        }
        ctx.fillStyle = "red";
        canvasWrapper.style.background = "red";
      } else if (offBy < -1 * difficulty.time) {
        streakCount = 0;
        missCountTooSlow = 0;
        missCountTooFast++;
        if (missCountTooFast > MISS_COUNT_HELP_TRIGGER) {
          helpString = "SLOW DOWN";
        }
        ctx.fillStyle = "red";
        canvasWrapper.style.background = "red";
      } else {
        streakCount++;
        missCountTooFast = 0;
        missCountTooSlow = 0;
        if (streakCount > 10) {
          helpString = "";
          for (let i = 0; i < streakCount - 10; i++) {
            helpString += "*";
          }
          ctx.fillStyle = "gold";
          canvasWrapper.style.background = "gold";
        } else {
          helpString = ":)";
          ctx.fillStyle = "green";
          canvasWrapper.style.background = "green";
        }
      }
      ctx.fillRect(0, 0, canvas.width, canvas.height);
      ctx.fillStyle = "white";
      ctx.font = "30px Comic Sans MS";
      ctx.fillText(helpString, center, 80);
      drawBpm(ctx, canvas, myBpm, center);
    }
  } else {
    oldTime = Date.now();
  }
  synth.start(mute);
}

function calculateUserBpm(bpm: number, difficulty: Difficulty, mute: boolean) {
  const canvas : HTMLCanvasElement | null = document.querySelector('#glCanvas');
  const ctx : CanvasRenderingContext2D | null = canvas ? canvas.getContext("2d") : null;
  const canvasWrapper : HTMLElement | null = document.getElementById("canvas-wrapper");
  const canvasSection : HTMLElement | null = document.getElementById("canvas-section");

  if (canvas && ctx && canvasWrapper && canvasSection) {
    const center = canvas.width / 2;
    drawInitialState(ctx, canvas, center, canvasWrapper);

    if (myEventListener) {
      canvasSection.removeEventListener('mousedown', myEventListener);
    }
    myEventListener  = function (event : MouseEvent) : any {
      mouseDownEventListener(bpm, ctx, canvasWrapper, canvas, center, difficulty, mute);
    };
    canvasSection.addEventListener('mousedown', myEventListener, false);
  }
}

export function TapArea() {
  const bpm = useSelector(selectBpm);
  const mute = useSelector(selectMute);
  const difficulty = useSelector(selectDifficulty);
  useEffect(function () {
    calculateUserBpm(bpm, difficulty, mute);
  },[]);
  calculateUserBpm(bpm, difficulty, mute);
  return <section id="canvas-section" className={styles.section}>
    <div id="canvas-wrapper" className={styles.canvasWrapper}>
      <canvas
          className={styles.canvas}
          onMouseDown={()=>{}}
          id="glCanvas" />
    </div>
  </section>;
}
