import { createSlice, Draft } from '@reduxjs/toolkit';
import { RootState } from "./store";
import { Difficulties, Difficulty } from "./Difficulties";

interface GameState {
  bpm: number;
  difficulty: Difficulty
  mute: boolean
}

const initialState : GameState = {
  bpm: 60,
  difficulty: Difficulties.LEVELS[1],
  mute: false
};

export const gameSlice = createSlice({
  name: 'game',
  initialState,
  reducers: {
    setBpm: (state, action) => {
      const bpm = parseFloat(action.payload);
      if (typeof bpm === 'number') {
        state.bpm = bpm;
      }
    },
    setDifficulty:  (state, action) => {
      const difficulty = parseFloat(action.payload);
      state.difficulty = Difficulties.LEVELS[difficulty];
    },
    setMute: (state: Draft<GameState>, action) => {
      const mute = action.payload;
      if (typeof mute === 'boolean') {
        state.mute = action.payload;
      }
    }
  }
});

export const { setBpm, setDifficulty, setMute } = gameSlice.actions;
export const  selectBpm = (state : RootState) => parseFloat(state.game.bpm.toFixed(1));
export const selectMute = (state: RootState) => state.game.mute;
export const  selectDifficulty = (state : RootState) => state.game.difficulty;

export default gameSlice.reducer;
