import React, { useState } from 'react';
import { synth, SubdivisionType } from "../../synth";
import ToggleButton from '@mui/material/ToggleButton';
import ToggleButtonGroup from '@mui/material/ToggleButtonGroup';
import styles from "./Subdivision.module.css";
import quarterNote from './quarter-note.png';
import sixteenthNote from './16th.svg';
import eightNote from './8th.png';
import eightNoteTriplet from './8th-triplet.webp';
import sixteenthNoteTriplet from './sixteenth-note-triplet.png';
import { FormLabel } from "@mui/material";

export function Subdivison() {
    const [subdivision, setSubdivision] = useState<SubdivisionType>('4n');

    const handleIntervalChange = (
        event: React.MouseEvent<HTMLElement>,
        newInterval: SubdivisionType,
    ) => {
        if (newInterval) {
            setSubdivision(newInterval);
            if (newInterval != "4n") {
                synth.startSubdivision(newInterval);
            } else {
                synth.stopSubdivision();
            }
        }
    };

    return <section className={styles.subdivision}>
        <FormLabel className={styles.label}>Subdivision</FormLabel>
        <ToggleButtonGroup
            aria-label="Subdivision"
            value={subdivision}
            exclusive
            onChange={handleIntervalChange}
        >
            <ToggleButton value="4n">
                <img src={quarterNote}/>
            </ToggleButton>
            <ToggleButton value="16n">
                <img src={sixteenthNote}/>
            </ToggleButton>
            <ToggleButton value="16t">
                <img src={sixteenthNoteTriplet}/>
            </ToggleButton>
            <ToggleButton value="8n">
                <img src={eightNote}/>
            </ToggleButton>
            <ToggleButton value="8t">
                <img src={eightNoteTriplet}/>
            </ToggleButton>
        </ToggleButtonGroup>
    </section>;
}