import * as Tone from 'tone';


import { Seconds } from "tone/Tone/core/type/Units";

let clickSynth: Tone.Synth;
let loopClick: Tone.Loop | null;

function start(mute: boolean) {
    if (!loopClick) {
        clickSynth = new Tone.Synth({
            oscillator: {
                type: 'sine',
            },
            envelope: {
                attack: 0,
                decay: 0.1,
                sustain: 0,
                release: 0.1,
            }
        }).toDestination();

        if (!mute) {
            loopClick = new Tone.Loop(click, '4n');
            loopClick.start(0);
        }
        Tone.Transport.start();
    }
}

function stop(mute: boolean) {
    if (loopClick && mute) {
        loopClick.stop();
        loopClick = null;
    }
    if (loopBeat) {
        loopBeat.stop();
        loopBeat = null;
    }
}

function click(time: Seconds) {
    clickSynth.triggerAttackRelease(440, '8n', time);
}

let bassSynth: Tone.MembraneSynth, loopBeat: Tone.Loop | null;

function subdivisionTriggerAttackRelease(time: Seconds) {
    bassSynth.triggerAttackRelease('c1', '8n', time);
}

function startSubdivision(interval: string) {
    if (loopBeat) {
        loopBeat.stop();
        bassSynth = new Tone.MembraneSynth().toDestination();
        loopBeat = new Tone.Loop(subdivisionTriggerAttackRelease, interval);
        Tone.Transport.start();
        loopBeat.start(0);
    }
    if (!loopBeat) {
        bassSynth = new Tone.MembraneSynth().toDestination();
        loopBeat = new Tone.Loop(subdivisionTriggerAttackRelease, interval);
        Tone.Transport.start();
        loopBeat.start(0);
    }
}

function stopSubdivision() {
    if (loopBeat) {
        loopBeat.stop();
        loopBeat = null;
    }
}

function setTransportBpm(bpm: number) {
    Tone.Transport.bpm.value = bpm;
}

function pause () {
    Tone.Transport.pause();
}

function resume () {
    Tone.Transport.start();
}

export type SubdivisionType = "4n" | "8n" | "16n" | "8t" | "16t";

export const synth = {
    start,
    stop,
    pause,
    resume,
    setTransportBpm,
    stopSubdivision,
    startSubdivision
};

