import React, { useEffect } from 'react';

import styles from './MetrodomeAnimation.module.css';
import { loadImage } from "../../images/image-loader";


import leftPngSrc from './metrodome-png/metronome-left.png';
import rightPngSrc from './metrodome-png/metronome-right.png';
import midLeftPngSrc from './metrodome-png/metronome-mid-left.png';
import midRightPngSrc from './metrodome-png/metronome-mid-right.png';
import centerPngSrc from './metrodome-png/metronome-center.png';
import * as Tone from "tone";

export function MetrodomeAnimation() {
    let index = 0;
    function draw (pngs: Array<HTMLImageElement>) {
        const canvas : HTMLCanvasElement = document.querySelector('#metrodome-canvas') as HTMLCanvasElement;
        if (canvas) {
            if (index === pngs.length) {
                index = 0;
            }
            const context2D = canvas.getContext('2d');
            if (context2D === null) {
                alert('Unable to initialize WebGL. Your browser or machine may not support it.');
                return;
            }

            const image = pngs[index];
            context2D.clearRect(0, 0, canvas.width, canvas.height);
            context2D.drawImage(
                image,
                0,0, image.width, image.height,
                0,0, canvas.width, canvas.height);
            index++;
        }
    }

    useEffect(()=> {
        (async function () {
            const [left, right, midLeft, midRight, center] = await Promise.all([
                loadImage(leftPngSrc),
                loadImage(rightPngSrc),
                loadImage(midLeftPngSrc),
                loadImage(midRightPngSrc),
                loadImage(centerPngSrc)
            ]);
            draw([center] as Array<HTMLImageElement>);
            let loopClick = new Tone.Loop((time)=>{
                Tone.Draw.schedule(function(){
                    draw([
                        center,
                        midRight,
                        right,
                        midRight,
                        center,
                        midLeft,
                        left,
                        midLeft
                    ] as Array<HTMLImageElement>);
                }, time);
            }, '16n');
            Tone.Transport.start();
            loopClick.start(0);
      })();
    }, []);


    return <section>
        <canvas className={styles.canvas}
                onMouseDown={() => {
                }}
                id="metrodome-canvas"/>
        <div className={styles.formWrapper}>
        </div>
    </section>;

}
