import React, { useState } from "react";
import styles from "./Bpm.module.css";
import { selectBpm, setBpm } from "../../app/gameSlice";
import { useAppDispatch, useAppSelector } from "../../app/hooks";

export function Bpm () {
    const bpm = useAppSelector(selectBpm);
    const [inputBpm, setInputBpm] = useState(bpm.toString());
    const dispatch = useAppDispatch();
    dispatch(setBpm(inputBpm));

    return <section className={styles.bpm}>
        <input className={styles.bpmInput}
               value={inputBpm}
               type="number"
               pattern="\d*"
               maxLength={3}
               onFocus={(event)=> {event.preventDefault();}}
               onChange={
                   (event) => {
                       const newBPM = parseFloat(event.target.value);
                       if (newBPM > 999) {
                           event.preventDefault();
                       } else if (!isNaN(newBPM)) {
                               setInputBpm(newBPM.toString());
                       } else {
                               setInputBpm("");
                       }
                   }
               }/>
        <span className={styles.targetBPM}>BPM</span>
    </section>;
}