import React, { ChangeEvent, useState } from "react";
import styles from './DifficultyView.module.css';
import { Difficulties, Difficulty } from "../../app/Difficulties";
import {
    FormControl,
    FormLabel,
    RadioGroup,
    FormControlLabel,
    Radio
} from "@mui/material";
import { useAppDispatch, useAppSelector } from "../../app/hooks";
import { setDifficulty, selectDifficulty } from "../../app/gameSlice";

function difficultyControl (difficulty : Difficulty, index: number) : JSX.Element {
    return (
        <FormControlLabel value={difficulty.value} key={index} control={<Radio />} label={difficulty.label} />
    );
}

export function DifficultyView () {
   const difficulty = useAppSelector(selectDifficulty);
    const dispatch = useAppDispatch();

   function handleChange (event: ChangeEvent<HTMLInputElement>, value: string) {
       dispatch(setDifficulty(value));
   }
   return (
    <FormControl>
       <FormLabel className={styles.radioLabel} id="difficulty-radio-buttons-group-label">Difficulty</FormLabel>
       <RadioGroup
           row
           aria-labelledby="difficulty-radio-buttons-group-label"
           value={difficulty.value}
           onChange={handleChange}
       >
           {Difficulties.LEVELS.map((item, index) => difficultyControl(item, index) )}
       </RadioGroup>
   </FormControl>
   );
}