export type ValidTime = 20 | 10 | 4 | 2;

export interface Difficulty {
    label: string;
    value: number;
    time: ValidTime;
}

let easy: Difficulty = { label:"Easy", value: 0, time: 20 };
let hard: Difficulty = { label:"Hard", value: 1, time: 10 };
let expert: Difficulty = { label: "Expert", value: 2, time: 4 };
let impossible: Difficulty = { label:"Impossible", value: 3, time: 2 };

export class Difficulties {
    static readonly LEVELS : Difficulty[] = [easy, hard, expert, impossible];
}