import React, { useState } from "react";
import styles from "./Controls.module.css";
import { Subdivison } from "./Subdivison";
import { DifficultyView } from './DifficultyView';
import MenuIcon from '@mui/icons-material/Menu';
import MenuOpenIcon from '@mui/icons-material/MenuOpen';
import IconButton from '@mui/material/IconButton';
import { Playback } from "./Playback";

export function Controls () {

    const [visible, setVisible] = useState(false);

    return <section>
        <header className={styles.headerSection}>
            <a href={"https://www.fierce.academy/"}>
                <img
                    src={"https://kajabi-storefronts-production.kajabi-cdn.com/kajabi-storefronts-production/file-uploads/themes/2149153955/settings_images/58fad8-46e8-162-d257-75bd328c0847_Fierce_logo_with_text_360_80_px_.png"}
                    className={styles.logo}
                    onClick={
                        (event)=>{event.stopPropagation();}
                    }/>
            </a>
            <Playback />
            <IconButton className={styles.controls}
                    onClick={() => {setVisible(!visible);}}>
                { visible ?
                    <MenuOpenIcon fontSize="medium" />
                    :
                    <MenuIcon fontSize="medium" />
                }
            </IconButton>
        </header>
        <div  className={visible ? styles.open : styles.closed}>
                <Subdivison/>
                <DifficultyView />
        </div>
    </section>;
}